h1,
h2,
h3,
p,
ul,
li {
  margin: 0;
}
ul,
li {
  list-style: none;
  text-decoration: none;
  padding: 0;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
  color: inherit;
}
address {
  font-style: normal;
}
button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}
*:focus-visible {
  outline: none;
}
